
"use client";

import React, { useState } from 'react';
import Link from 'next/link';
import { ChevronUp, Zap, Clock, TrendingUp } from 'lucide-react';
import { Agent } from '../types';

interface AgentCardProps {
  agent: Agent;
}

const AgentCard: React.FC<AgentCardProps> = ({ agent }) => {
  const [upvotes, setUpvotes] = useState(agent.upvotes_count);
  const [hasUpvoted, setHasUpvoted] = useState(agent.has_upvoted);

  const handleUpvote = (e: React.MouseEvent) => {
    e.preventDefault();
    e.stopPropagation();
    if (hasUpvoted) {
      setUpvotes(prev => prev - 1);
      setHasUpvoted(false);
    } else {
      setUpvotes(prev => prev + 1);
      setHasUpvoted(true);
    }
  };

  return (
    <Link
      href={`/agent/${agent.slug}`}
      className="group block bg-[#121212] border border-white/10 rounded-xl p-5 hover:bg-[#181818] transition-all glow-hover relative"
    >
      <div className="flex gap-6 items-start">
        {/* Upvote Button */}
        <button
          onClick={handleUpvote}
          className={`flex flex-col items-center justify-center min-w-[50px] h-[60px] rounded-lg border transition-all ${hasUpvoted
            ? 'bg-blue-600/20 border-blue-500/50 text-blue-400'
            : 'bg-white/5 border-white/10 text-white/40 group-hover:border-white/30'
            }`}
        >
          <ChevronUp className={`w-5 h-5 mb-1 transition-transform ${hasUpvoted ? 'translate-y-[-2px]' : ''}`} />
          <span className="text-sm font-bold">{upvotes}</span>
        </button>

        <div className="flex-1">
          <div className="flex items-center justify-between mb-2">
            <h3 className="text-xl font-bold group-hover:text-blue-400 transition-colors">
              {agent.name}
            </h3>
            <span className="text-xs font-mono uppercase tracking-widest px-2 py-1 bg-white/5 rounded border border-white/10 text-white/50">
              {agent.framework}
            </span>
          </div>

          <p className="text-white/60 text-sm mb-4 line-clamp-1">
            {agent.tagline}
          </p>

          <div className="flex flex-wrap items-center gap-4">
            <div className="flex items-center gap-1.5 text-xs text-white/40">
              <Zap className="w-3.5 h-3.5 text-blue-400" />
              <span>Success Rate: <span className="text-green-400 font-bold">{agent.success_rate}%</span></span>
            </div>
            <div className="flex items-center gap-1.5 text-xs text-white/40">
              <Clock className="w-3.5 h-3.5 text-blue-400" />
              <span>Latency: <span className="text-white font-medium">{agent.latency_avg}ms</span></span>
            </div>
            <div className="flex items-center gap-1.5 text-xs text-white/40">
              <TrendingUp className="w-3.5 h-3.5 text-blue-400" />
              <span>Cost: <span className="text-white font-medium">${agent.cost_per_task}</span></span>
            </div>
          </div>
        </div>

        <div className="hidden lg:flex flex-col items-end gap-2">
          <img
            src={agent.creator?.avatar_url}
            alt={agent.creator?.username}
            className="w-8 h-8 rounded-full border border-white/20"
          />
          <span className="text-[10px] font-mono text-white/20 uppercase">By {agent.creator?.username}</span>
        </div>
      </div>
    </Link>
  );
};

export default AgentCard;
