
import React from 'react';

interface FilterBarProps {
  current: string;
  onChange: (framework: string) => void;
}

const frameworks = ['All', 'OpenClaw', 'CrewAI', 'LangChain', 'AutoGPT'];

const FilterBar: React.FC<FilterBarProps> = ({ current, onChange }) => {
  return (
    <div className="flex items-center gap-2 mb-8 overflow-x-auto pb-2 scrollbar-hide">
      <span className="text-sm font-mono text-white/20 uppercase tracking-widest mr-4">Filter:</span>
      {frameworks.map((fw) => (
        <button
          key={fw}
          onClick={() => onChange(fw)}
          className={`px-4 py-1.5 rounded-full text-sm font-medium transition-all whitespace-nowrap border ${
            current === fw 
            ? 'bg-blue-600 border-blue-500 text-white shadow-lg shadow-blue-600/20' 
            : 'bg-white/5 border-white/10 text-white/60 hover:border-white/20 hover:text-white'
          }`}
        >
          {fw}
        </button>
      ))}
    </div>
  );
};

export default FilterBar;
